<template lang="pug">
include ../../../mixins/bemto/bemto
+b.knob(ref="knob")
  round-slider(
    :value="song.bpm",
    :min="constants.minBpm",
    :max="constants.maxBpm",
    line-cap="round",
    radius="105",
    :update="startInput",
    :change="stopInput",
    :animation="false"
  )
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
export default {
  methods: {
    ...mapMutations(['CHANGE_CURRENT_BPM']),
    startInput(event) {
      this.CHANGE_CURRENT_BPM(event.value)
      this.$refs.knob.classList.add('knob--glow')
    },
    stopInput() {
      this.$refs.knob.classList.remove('knob--glow')
    },
  },
  computed: {
    ...mapGetters(['song', 'constants']),
  },
}
</script>
